/*===============================
=            Globals            =
===============================*/
body{
	background-color: black;
    margin: 0px;
}
a:focus, a:hover{
    text-decoration: none;
}
::-webkit-scrollbar{
    display: none !important;
}

/*=============================
=            Boxes            =
=============================*/
.box-viewport {
	position:relative;
	width: 100vw;
	padding-bottom: 100vh;
}
.box-viewport-75 {
	width: 100vw;
	position:relative;
	padding-bottom: 75vh;
}
.box-viewport-25 {
	width: 100vw;
	position:relative;
	padding-bottom: 25vh;
}

.box {
   float: left;
   position: relative;
   width: 100%;
   padding-bottom: 56.25%;
}

.boxInner-viewport {
    position: absolute;
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    overflow: hidden;
 }
 .boxInner-content{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    z-index: 20;
 }

 .box-img {
     display: inline-block;
     background-position: center center;
     background-repeat: no-repeat;
 }

.boxInner .titleBox{
   position: absolute;
   display: none;
   bottom: 0;
   left: 0;
   top:0;
   right: 0;
   color:transparent;
   font-size: 1.3em;
   text-align: center;
   background: #000;
   background: rgba(0, 0, 0, 0);
   opacity: 0;
   -webkit-transition: background-color 0.5s ease, opacity .5s;
   -moz-transition: background-color 0.5s ease, opacity .5s;
   -o-transition: background-color 0.5s ease, opacity .5s;
   transition: background-color 0.5s ease, opacity .5s;
 }
.boxInner:hover .titleBox{
     display:block;
     position: absolute;
     width:100%;
     bottom: 0;
     left: 0;
     right: 0;
     top: 0;
     background: #000;
     background: rgba(0, 0, 0, 0.5);
     opacity: 1;
     color: #FFF;
     margin:0px 0 0 0px;
     font-weight:500;
 }
 .frame {
 	position: absolute;
 	top: -120%;
 	width: 100%;
 	-webkit-transition: 1500ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
 	-moz-transition: 1500ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
 	-o-transition: 1500ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
 	transition: 1500ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
 	-webkit-transform: translateZ(0);background-color: black;
 }

 .frame.active {
 	position: absolute;
 	top: 0%;
 }
 .viewport-scroll {
 	width: 100vw;
 	height: 100vh;
 	padding-top: 1px;
 	padding-bottom: 1px;
 	box-sizing: content-box;
 }
/* ===================================
 =            Transitions            =
 ===================================*/
 .img-render-transition{
    -webkit-transition: opacity .5s ease-out;
    -moz-transition: opacity .5s ease-out;
    -o-transition: opacity .5s ease-out;
    -ms-transition: opacity .5s ease-out;
    transition: opacity .5s ease-out;
 }
 .text-render-transition{
    -webkit-transition: opacity 1s ease-out;
    -moz-transition: opacity 1s ease-out;
    -o-transition: opacity 1s ease-out;
    -ms-transition: opacity 1s ease-out;
    transition: opacity 1s ease-out;  
 }
 .button-render-transition{
    -webkit-transition: 1200ms cubic-bezier(0.77, 0, 0.175, 1);
    -moz-transition: 1200ms cubic-bezier(0.77, 0, 0.175, 1);
    -o-transition: 1200ms cubic-bezier(0.77, 0, 0.175, 1);
    transition: 1200ms cubic-bezier(0.77, 0, 0.175, 1);
 }
 /*=====================================
 =            Style Classes            =
 =====================================*/
 .no-padding {
     padding: 0;
 }
 .size-fixed {
     width: 100%;
     height: 100%;
 }
 /*===============================
 =            Buttons            =
 ===============================*/
 a.stylized,button.stylized {
     -moz-appearance: none;
     -webkit-appearance: none;
     -o-appearance: none;
     -ms-appearance: none;
     appearance: none;
     background-color: transparent;
     border-radius: 3px;
     border: 0;
     box-shadow: inset 0 0 0 1px #5e55a8;
 /*color: #555 !important;*/
     color: #5e55a8;
     cursor: pointer;
     display: inline-block;
     font-size: 18px;
     font-weight: 400;
     height: 2.75em;
     letter-spacing: .125em;
     line-height: 2.75em;
     padding: 0 2em;
     text-align: center;
     vertical-align: middle;
     text-decoration: none;
     text-indent: .125em;
     text-transform: uppercase;
     white-space: nowrap;
 /*padding: 5px 0 5px 0 !important;*/
 }

 a.stylized:hover,button.stylized:hover {
     background-color: #5e55a8;
     color: #fff;
 /*        box-shadow: inset 0 0 0 1px #555;
